<template>
    <v-card outlined>
        <v-card-text>
            <v-row class="text-center" style="min-height:150px; padding-top: 10px">
                <v-col cols="12">
                    <p style="font-size: 60px; color: mediumspringgreen; text-shadow: 2px 2px green;">
                        {{verb.hiragana}}
                    </p>
                </v-col>

                <v-col cols="12">
                    <p style="font-size: 25px">
                        <a :href="jishoLink" class="text--secondary" target="_blank">
                            {{verb.dict}}
                        </a>
                    </p>
                    <v-btn :ripple="false"   @click="say(verb.example)">
                        {{verb.example}}
                        <v-icon>volume_up</v-icon>
                    </v-btn>
                    <p>
                        <!-- {{verb.romaji}}-->
                        {{verb.exampleTranslation}}
                    </p>
                </v-col>
                <v-col cols="12">
                    <p class="text--primary" style="font-size: 24px">
                        {{verb.meaning}}
                    </p>
                </v-col>
            </v-row>
        </v-card-text>
    </v-card>
</template>

<script>


    export default {
        name: "QuestionCard",
        methods: {
            say(word) {
                const msg = new SpeechSynthesisUtterance();
                msg.volume = 1;
                msg.rate = 0.5;
                msg.pitch = 0.8;
                msg.text = word;
                msg.lang = 'ja-JP';
                speechSynthesis.speak(msg);
            }
        },
        computed: {
            jishoLink() {
                return `https://jisho.org/search/${this.verb.dict}`;
            }
        },
        props: {
            verb: {
                type: Object,
                required: true,
            }
        },
    }
</script>
